.App{text-align:center;padding:2% 5%;background-color:#0a1931;color:#efefef}.App *{box-sizing:border-box}ul{padding:0;list-style:none}.display-none{display:none}.showSuggestions{display:block}input[type=text],input[type=number]{padding:8px 6px 8px 10px;border-radius:4px;outline:none;border:1px solid #ccc;font-size:14px;line-height:20px;-webkit-align-self:center;align-self:center;height:38px}.city{width:250px}.CitySearch,.NumberOfEvents{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-basis:300px;flex-basis:300px;max-width:300px;margin:4% auto 3%;position:relative}.suggestions{width:250px;position:absolute;z-index:2;-webkit-align-self:center;align-self:center;margin:0;top:43px;font-size:14px;box-shadow:10px 10px 16px -12px rgba(0,0,0,.75);color:#000}.suggestions>li{text-align:left;padding:8px 6px 8px 10px;background-color:#eee;cursor:default}.suggestions>li:last-child{border-bottom-left-radius:4px;border-bottom-right-radius:4px}.suggestions>li:hover{background-color:#bcdcdb}.event-container{padding:10px 10px 35px;margin-bottom:10px;box-shadow:0 1px 2px rgba(0,0,0,.07),0 2px 4px rgba(0,0,0,.07),0 4px 8px rgba(0,0,0,.07),0 8px 16px rgba(0,0,0,.07),0 16px 32px rgba(0,0,0,.07),0 32px 64px rgba(0,0,0,.07);background:#2a3e5e;border-radius:4px;position:relative}.show-hide-btn{display:inline-block;padding:7px 12px;font-size:110%;cursor:pointer;text-align:center;text-decoration:none;outline:none;color:#efefef;background-color:#ffc947;border:none;border-radius:15px;box-shadow:0 2px #999}.show-hide-btn:hover{background-color:#ffc847d0}.show-hide-btn:active{background-color:#ffc8476e;box-shadow:0 1px #666;-webkit-transform:translateY(4px);transform:translateY(4px)}.name{font-size:18px;font-weight:600}.group-name{text-transform:uppercase;font-size:14px}.going,.visibility{font-style:italic}.description{overflow-x:hidden}.button-wrapper{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:20px;padding:20px;font-family:sans-serif}.Alert{position:absolute;bottom:30px;font-size:85%}.data-vis-wrapper{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.data-vis-wrapper .recharts-responsive-container{min-width:100%;display:-webkit-flex;display:flex}.recharts-surface{overflow:visible}.privacy{padding-bottom:20px;font-size:85%}@media only screen and (min-width: 992px){.data-vis-wrapper{-webkit-flex-direction:row;flex-direction:row}.data-vis-wrapper .recharts-responsive-container{-webkit-flex-basis:50%;flex-basis:50%;max-width:50%;min-width:auto;margin:0 auto}}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.WelcomeScreen {
  width: 100%;
  height: 100%;
  top: 0;
  background: #2A3E5E;
  box-shadow: 0 1px 2px rgba(0,0,0,0.07), 
                0 2px 4px rgba(0,0,0,0.07), 
                0 4px 8px rgba(0,0,0,0.07), 
                0 8px 16px rgba(0,0,0,0.07),
                0 16px 32px rgba(0,0,0,0.07), 
                0 32px 64px rgba(0,0,0,0.07);
  padding-bottom: 1%;
  border-radius: 4px;
}
.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  margin-bottom: .2rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
}

